.head__logo-tagline {
    $showing-time: 3s;
    .logo {
        float: left;
        width: rem(265);
        margin-right: rem(25);
        @include f(m-semibold);
        font-size: rem(10); //20px;
        line-height: 1.5;
        letter-spacing: rem(6);
        text-transform: uppercase;
        color: #333333;
        margin-top: rem(10);
        //opacity: 0;
        //visibility: hidden;
        transition: opacity $showing-time,
        visibility $showing-time,
        color .5s;
        //@include respond-to(desktop) {
        //    //width: 120px;
        //    margin-bottom: 6px;
        //    font-size: 10px; //14px;
        //    letter-spacing: 4px;
        //}
        &.logo_big {
            display: block;
        }
        &.logo_small {
            display: none;
        }
        .dark &, .log-in-state & {
            color: white;
        }
    }
    .tagline {
        //float: left;
        //width: 165px;

        @include f(p-regular);
        font-size: 18px; //20px;
        line-height: 130%;
        color: #AAAAAA;

        opacity: 0;
        visibility: hidden;
        transition: opacity $showing-time, visibility $showing-time;
        transition-delay: $showing-time * .25;
        //@include respond-to(desktop) {
        //    //width: 110px;
        //    font-size: 12px;
