.log-in {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1004;
    overflow: hidden;

    background-color: #333333;
    transition: transform .75s;

    .head__logo-tagline {
        position: relative;
        margin-top: rem(44);
        margin-bottom: rem(50);
        //@include respond-to(desktop) {
        //    margin-top: 44px;
        //    margin-bottom: 50px;
        //}
        .mail-n-password {
            height: rem(50);
            //@include respond-to(desktop) {
            //    height: 50px;
            //}
            .input-field {
                position: absolute;
                top: 0;
                width: 47%;
            }
            .input-field_email {
                left: 0;
            }
            .input-field_password {
                right: 0;
            }
            .helper-btn {
                position: absolute;
                left: 53%;
                top: 100%;
                @include f(p-regular);
                font-size: rem(8);
                letter-spacing: rem(4);
                text-transform: uppercase;
                color: white;
                cursor: pointer;
                transform: translateY(-8px);
                transition: left .35s;
                //@include respond-to(desktop) {
                //    font-size: 8px;
                //    letter-spacing: 4px;
                //    transform: translateY(-8px);
                //}
                .helper-btn__content {
                    position: absolute;
                    left: 0;
                    top: 0;
                    white-space: nowrap;
                    transition: visibility .35s,
                    opacity .35s;
                    &.hidden {
                        visibility: hidden;
                        opacity: 0;
                    }
                }
                .helper-btn__content_back {
                    padding-left: rem(50);
                    //@include respond-to(desktop) {
                    //    padding-left: 50px;
                    //}
                    &:before {
                        content: '';
                        display: block;
                        position: absolute;
                        left: 0;
                        top: 40%;
                        width: rem(33);
                        height: rem(2);
                        background-color: #AAAAAA;
                        transform: translateY(-50%);
                        //@include respond-to(desktop) {
                        //    width: 33px;
                        //}
                    }
                    &:after {
                        content: '';
                        display: block;
                        position: absolute;
                        left: 0;
                        top: 40%;
                        width: rem(4);
                        height: rem(4);
                        border-left: 2px solid #AAAAAA;
                        border-bottom: 2px solid #AAAAAA;
                        transform: translateY(-50%) rotate(45deg);
                        //@include respond-to(desktop) {
                        //    width: 4px;
                        //    height: 4px;
                        //}
                    }
                }
            }
            &.recover-pswd-state {
                .input-field_email {
                    width: 100%;
                }
                .input-field_password {
                    visibility: hidden;
                    opacity: 0;
                }
                .helper-btn {
                    left: 0;
                }
            }
        }
    }
    .sub-header {
        @include f(p-regular);
        font-size: rem(14);
        line-height: 120%;
        color: white;
        margin-bottom: rem(120);
        max-width: rem(950);
        //@include respond-to(desktop) {
        //    font-size: 14px;
        //    margin-bottom: 120px;
        //    max-width: 950px;
        //}
    }
    &.hidden {
        transform: translateY(-100%);
    }
}

