.enter-ico {
    position: absolute;
    right: 8px;
    top: 3px;
    width: 6px;
    height: 3px;
    box-sizing: content-box;
    border-right: 2px solid #AAAAAA;
    border-bottom: 2px solid #AAAAAA;
    //@include respond-to(desktop) {
    //    right: 8px;
    //    top: 3px;
    //    width: 6px;
    //    height: 3px;
    //}
    &:before {
        content: '';
        position: absolute;
        display: block;
        width: 1px;
        height: 1px;
        border: 4px solid transparent;
        border-right-color: #AAAAAA;
        left: -9px;
        bottom: -6px;
        //@include respond-to(desktop) {
        //    border: 4px solid transparent;
        //    border-right-color: #AAAAAA;
        //    left: -9px;
        //    bottom: -6px;
        //}
    }
}

.close-btn {
    position: absolute;
    right: $page-padding-right;
    top: rem(18);
    transform: translateY(-50%);
    width: 20px;
    height: 20px;
    pointer-events: visiblePainted;
    //@include respond-to(desktop) {
    //    right: $page-padding-right_desktop;
    //}
    &:before, &:after {
        content: '';
        display: block;
        position: absolute;
        width: 0;
        height: 2px;
        background-color: #AAAAAA;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%) rotate(45deg);
        transition: all .35s;
        .log-in-state & {
            width: 16px;
            //@include respond-to(desktop) {
            //    width: 16px;
            //}
        }
    }
    &:after {
        transform: translate(-50%, -50%) rotate(-45deg);
    }
    .log-in-state & {
        cursor: pointer;
        &:hover {
            &:before, &:after {
                width: 14px;
                //@include respond-to(desktop) {
                //    width: 14px;
                //}
            }
        }
    }
}

.main-btn-wrap {
    position: absolute;
    right: rem(70);
    top: rem(18);
    transform: translateY(-50%);
    padding-right: rem(22);
    transition: all .5s;
    //@include respond-to(desktop) {
    //    right: $page-padding-right_desktop;
    //    padding-right: 22px;
    //}
    .enter-ico {
        visibility: hidden;
        opacity: 0;
        transition: all .35s;
    }
    .log-in-state & {
        right: rem(70); // + 120px;
        //@include respond-to(desktop) {
        //    right: $page-padding-right_desktop; // + 80px;
        //}
        .enter-ico {
            visibility: visible;
            opacity: 1;
        }
    }
}

.main-btn {
    position: relative;
    @include f(m-semibold);
    font-size: rem(10);
    letter-spacing: rem(4);
    color: #333333;
    text-transform: uppercase;
    text-align: center;
    padding-left: rem(15);
    padding-right: rem(6);
    white-space: nowrap;
    cursor: pointer;
    transition: opacity 1s,
    visibility 1s;
    pointer-events: visiblePainted;
    //@include respond-to(desktop) {
    //    font-size: 10px;
    //    letter-spacing: 4px;
    //}
    .main-btn__hover-wrap {
        position: absolute;
        z-index: 2;
        left: 0;
        top: 0;
        width: 0;
        overflow: hidden;
        visibility: hidden;
        opacity: 0;
        transition: width .35s,
        visibility 0s linear .35s,
        opacity 0s linear .35s;
    }
    .main-btn__hover {
        background-color: #333333;
        color: white;
        padding-left: rem(15);
        padding-right: rem(7);
        box-sizing: border-box;
    }
    &:hover {
        .main-btn__hover-wrap {
            width: 100%;
            visibility: visible;
            opacity: 1;
            transition: width .35s,
            visibility 0s linear 0s,
            opacity 0s linear 0s
        }
    }
    &.hidden {
        opacity: 0;
        visibility: hidden;
    }
    .dark &, .log-in-state & {
        color: white;
        .main-btn__hover {
            background-color: white;
            color: #333333;
        }
    }
}

.input-field {
    position: relative;
    height: rem(50);
    transition: all .35s;
    //@include respond-to(desktop) {
    //    height: 50px;
    //}
    input[type="text"], input[type="email"], input[type="password"] {
        display: block;
        position: relative;
        height: rem(27);
        @include f(p-semibold);
        font-size: rem(14);
        color: white;
        line-height: rem(27);
        padding: 0;
        background-color: transparent;
        border: 0 !important;
        outline-width: 0;
        transform: translateY(5%);
        width: 100%;
        //@include respond-to(desktop) {
        //    height: 27px;
        //    font-size: 14px;
        //    line-height: 27px;
        //}
    }
    label {
        position: absolute;
        left: 0;
        top: 35%;
        @include f(m-semibold);
        font-size: rem(10);
        letter-spacing: rem(4);
        text-transform: uppercase;
        color: #AAAAAA;
        pointer-events: none;
        transform-origin: 0 0;
        transform: translateY(-50%);
        transition: all .35s;
        //@include respond-to(desktop) {
        //    font-size: 10px;
        //    letter-spacing: 4px;
        //}
        &.minified {
            top: rem(-6);
            transform: scale(.75) translateY(0);
            //@include respond-to(desktop) {
            //    top: -6px;
            //}
        }
    }
    .border-line {
        position: relative;
        height: 2px;
        background-color: #484848;
        transform: translateY(6px);
        &:before, &:after {
            content: '';
            display: block;
            position: absolute;
            width: 0;
            left: 0;
            bottom: 0;
            height: 2px;
            background-color: white;
            transition: all .35s;
        }
        &:after {
            width: 100%;
            visibility: hidden;
            opacity: 0;
            background-color: transparent;
            transition: all .35s;
        }
        &.active {
            &:before {
                width: 100%;
            }
            &:after {
                visibility: visible;
                opacity: 1;
            }
        }
    }
    &.strength_0 {
        .border-line:after {
            background-color: red;
        }
    }
    &.strength_1 {
        .border-line:after {
            background-color: orangered;
        }
    }
    &.strength_2 {
        .border-line:after {
            background-color: yellow;
        }
    }
    &.strength_3 {
        .border-line:after {
            background-color: greenyellow;
        }
    }
    &.strength_4 {
        .border-line:after {
            background-color: green;
        }
    }
}

