.header {
  position: relative;
  margin-bottom: rem(80); }

.header--page {
  .header__login-group {
    white-space: nowrap; }
  @include xs {
    .header__login-group {
      //display: flex
      display: none;
      flex-direction: column;
      align-items: flex-start; }
    .header__btn-separator {
      width: 100%;
      margin: 5px 0; }
    .link {
      text-align: left;
      width: auto; } } }

.header--mini {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  margin: 0;
  min-width: $min-width;
  background: $color-text-main;
  color: white;
  transform: translateY(-100%);
  transition: transform 0.35s ease;
  // box-shadow: 0 20px 50px rgba(0, 0, 0, 0.5)
  .header__left,
  .header__login-group {
    display: flex;
    align-items: center; }
  .header__content {
    min-height: rem(30);
    padding-top: 0;
    align-items: center; }
  .header__btn-separator {
    margin: 0 rem(4); }
  .back-btn-group {
    display: none;
    top: rem(-2);
    margin: 0;
    line-height: 1.5;
    .arrow-line {
      top: rem(7);
      right: 0; }
    &:not(:hover) {
      .arrow-line {
        opacity: 0.4; } } }
  &.is-visible {
    transform: none; }
  @include xs {
    padding-left: $outer-padding-sm;
    .header__content {
      //display: none
      padding: 0;
      min-height: rem(40);
      align-items: center; }
    .header__login-group {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      padding-right: rem(35); }
    .header__btn-separator {
      position: relative;
      top: rem(-9);
      width: calc(100% + 3.5rem);
      opacity: 0.3; }
    .mini-logo,
    .mini-logo-sub {
      display: none; }
    .back-btn-group {
      display: block; }
    .mini-logo-sub {
      display: none; } } }

.header--mini-hub {
  .back-btn-group {
    display: none; }
  @include xs {
    .header__content {
      min-height: 30px; }
    .header__login-group {
      flex-direction: row;
      align-items: center;
      padding: 0; }
    .header__btn-separator {
      width: rem(20);
      top: 0;
      opacity: 1; }
    .back-btn-group {
      display: none; }
    .mini-logo {
      display: inline-block; } } }


.header__bar {
  position: fixed;
  background: $color-text-main;
  height: rem(8);
  top: 0;
  right: 0;
  left: $outer-padding;
  @include md {
    left: $outer-padding-md; } }

.header__content {
  display: flex;
  justify-content: space-between;
  padding-top: rem(30); }

.header__logo {
  display: inline-block;
  @include f(m-semibold, 10, 15, 500);
  text-transform: uppercase;
  color: inherit;
  text-decoration: none; }

.header__logo-second-line {
  display: block;
  margin-top: rem(3);
  @include f(p-regular, 12, 15);
  color: $color-text-grey;
  @include sm {
    font-size: rem(14); } }

.header__text {
  margin-top: rem(30);
  font-size: rem(14);
  line-height: (20/14);
  @include sm {
    font-size: inherit; } }

//.header__login-group
//  +sm
//    display: none

.header__btn-separator {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  border-top: 1px solid;
  width: rem(50); }

.mini-logo {
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  text-decoration: none;
  color: inherit;
  @include f(m-semibold, 10, 10, 500);
  @include sm {
    .mini-logo__line {
      width: rem(20); } } }

.mini-logo__line {
  margin: 0 8px;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  border-top: rem(1) solid;
  width: rem(50); }

.mini-logo-sub {
  display: inline-block;
  margin-left: rem(20);
  font-size: rem(12);
  line-height: 1;
  color: $color-text-grey; }
